nav {
  height: 70px;
  display: flex;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
  padding: 15px;
}

section {
  padding: 20px;
}

.gray {
    color: #918E9B !important;
}

.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.hero--photo {
  max-width: 800px;
  align-items: center;
}

.hero--header {
    margin-bottom: 16px;
    align-self: flex-start;
    padding-left: 340px;
}

.hero--text {
    margin-top: 0;
    align-self: flex-start;
    padding-left: 340px;
}

.cards-list {
    display: flex;
    justify-content: center;
    flex-wrap: nowrap;
    gap: 20px;
    overflow-x: auto;
}

.card {
  width: 300px;
  font-size: 14px;
  display: flex;
  flex: 0 0 auto;         
  display: flex;          
  flex-direction: column; 
  position: relative;
}

.card--image {
  width: 100%;
  height: 400px;
  border-radius: 10px;
  margin-bottom: 15px;
}

.card--stats {
  display: flex;
  align-items: center;
}

.card--title {
    overflow: hidden;
    text-overflow: ellipsis;
}

.card--price {
    margin-top: auto;
}

#location {
  margin-left: 10px;
}

.card--badge {
  position: absolute;
  top: 6px;
  left: 6px;
  background-color: white;
  padding: 5px 7px;
  border-radius: 2px;
  font-weight: bold;
}